
import 'whatwg-fetch'
import queryString from 'query-string';
import { googleScriptUri } from './constants';

import 'normalize.css/normalize.css';
import './styles/index.scss';

let isSubmitting = false;
document.addEventListener("DOMContentLoaded", () => {
    const incidentForm = document.getElementById("incidentForm");
    incidentForm.onsubmit = event => {
        event.preventDefault();
        if(isSubmitting) return;
        toggleIsSubmitting();
        clearIncidentFormMessage();
        const urlLink = getUrlLink();
        if (!urlLink || !isValidUrl(urlLink)) {
            setIncidentFormMessage("You must provide a valid url", "red", false);
            toggleIsSubmitting();
        } else {
            submitResponse({
                url_link: urlLink
            }).then(() => {
                setIncidentFormMessage("Thanks for your submission", "green", false);
                resetUrlLink();
                toggleIsSubmitting();
            }).catch(() => {
                setIncidentFormMessage("Somthing went wrong, please try again", "red", false);
                toggleIsSubmitting();
            });
        }
    };
});

const toggleIsSubmitting = () => {
    isSubmitting = !isSubmitting;
    if (isSubmitting) {
        const submitButton = getSubmitButton();
        const spinner = getSpinner();
        spinner.className = "spinner";
        submitButton.style.display = "none";
    } else {
        const submitButton = getSubmitButton();
        const spinner = getSpinner();
        spinner.className = "";
        submitButton.style.display = "block";
    }
}

const getUrlLink = () => {
    const urlLinkInput = document.getElementById("urlLink");
    return urlLinkInput.value;
}

const getSubmitButton = () => {
    return document.getElementById("submitIncident");
}

const getSpinner = () => {
    return document.getElementById("spinner");
}

const resetUrlLink = () => {
    const urlLinkInput = document.getElementById("urlLink");
    urlLinkInput.value = "";
}

const setIncidentFormMessage = (value, color, hide = false) => {
    const element = document.getElementById("incidentFormMessage");
    element.innerText = value;
    element.style.display = hide ? "none" : "";
    element.style.color = color;
}

const clearIncidentFormMessage = () => {
    setIncidentFormMessage("", null, true);
}

const isValidUrl = (url) => {
    var expression = /[-a-zA-Z0-9@:%_\+.~#?&//=]{2,256}\.[a-z]{2,4}\b(\/[-a-zA-Z0-9@:%_\+.~#?&//=]*)?/gi;
    var regex = new RegExp(expression);
    return url.match(regex) ? true : false;
}

const submitResponse = (data) => {
    const queryStrings = queryString.stringify(data);
    return fetch(`${googleScriptUri}?${queryStrings}`)
    .then(response => response.json());
}